import React from 'react'
import Link from 'gatsby-link'

import './index.css'

const IndexPage = () => (
  <div className="home">
    <section id="hero">
      <div className="container">
        <h2>Menumbuhkan Lebih Banyak Problem-solver</h2>
        <p>Berbagi dan Bersinergi</p>
      </div>
    </section>
    <section id="the-problem">
      <div className="container">
        <div className="row">
          <div className="col-xs-12 col-sm-6">
            <h3 className="crisp crisp--400">Permasalahan</h3>
            <p>Teknologi berkembang dengan sangat cepat namun persebaran pengetahuan tentang teknologi belum merata. Di kota Palu, masih sedikit orang-orang yang melek tentang teknologi dan praktis-praktis yang terbaru di Industri Teknologi.</p>
          </div>
        </div>
      </div>
    </section>
    <section id="our-vision">
      <div className="container">
        <div className="row">
          <div className="col-xs-12 col-sm-6">
            <h3 className="crisp crisp--400">Visi Kami</h3>
            <p>Kami percaya pengetahuan tentang teknologi adalah hak semua orang. Kami ingin berdiri sejajar dengan kota-kota besar di Indonesia bahkan di luar negeri dalam pengetahuan dan ekosistem. Meluaskan pengetahuan dan penerapan teknologi untuk memecahkan berbagai masalah.</p>
          </div>
        </div>
      </div>
    </section>
  </div>
)

export default IndexPage



